/* ------------DnD basic styling; shrink is used in dnd2 in smartin ---------------*/
.draggable img {
    left: 0;
    top: 0;
}

.draggable {
    cursor: pointer;
}

.shrink {
    transform: scale(0);
    -ms-transform: scale(0);
    transition: all .2s ease-in-out;
    /* if you need keyboard accessibility to work properly for dnd2 then add display:none to shrink class */
    /* display: none; */
}

