@font-face {
	font-family: VideoJS;
	src: url("font/VideoJSInteractive.eot?#iefix") format("eot")
}

@font-face {
	font-family: VideoJS;
	src: url("font/VideoJSInteractive.woff") format("woff");
	font-weight: 400;
	font-style: normal
}

@font-face {
	font-family: VideoJS;
	src: url('font/VideoJSInteractive.ttf') format('truetype');
	font-weight: 400;
	font-style: normal
}


.video-js .vjs-big-play-button .vjs-icon-placeholder:before,
