.reveal {
    width: 100vw;
    height: 56.25vw;
    max-height: 100vh;
    max-width: 177.78vh;
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    box-sizing: border-box;
}

/* adds box shadow to reveal slides */
.shadow {
    -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
}

.slides {
    background-color: white;
}

button {
    background-color: transparent;
    color: inherit;
    border-width: 0;
    padding: 0;
    cursor: pointer;
}

.dev-mode {
    background-color: $si-main-color;
}

body {
    font-family: $si-main-font;
    font-weight: 300;
}

section {
    height: inherit;
    position: absolute !important;
    transform-origin: 0 0 0;
}

section img,
section div {
    position: absolute;
}

#guidelines {
    display: none;
}


