.threedb {
    position: absolute;
    transition: all .5s ease;
    width: 15%;
    height: 46%;

    .item-pic {
        @include full-size;

        img {
            @include full-size;
        }
    }

    .item-text {
        top: 60%;
        left: 50%;
        transform: translateX(-50%);
        height: 35%;
        width: 69%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 0.7rem !important;
        z-index: 1;

        p {
            transition: all .3s ease-in-out;
            background: black;
            color: white;
            padding: 0.1rem 0.15rem;
        }
    }

    &:hover {
        transform: translateX(-50%) scale(1.1);
    }
}

.button {
    height: 10%;
    width: 6%;
    z-index: 3;
    top: 83%;
    cursor: pointer;

    &.leftButton {
        left: 27%;
    }

    &.rightButton {
        left: 67%;
    }
}

//change spread to increase disctance between objects
$spread: 2;

//change angle to change...uhhh..the angle
$angle: 2;

//move everything up
$move-up: 0;


//to edit distance between items in row change decimal number 

//even number of elements
$third-row-left-even: $spread * 10%+50%;
$second-row-left-even: (($third-row-left-even - 50%) * 0.75)+50%;
$fourth-row-left-even: (($third-row-left-even - 50%) * 0.59)+50%;

//odd number of elements*/
$third-row-left-odd: $spread * 10%+50%;
$second-row-left-odd: (($third-row-left-odd - 50%) * 0.9)+50%;
$fourth-row-left-odd: (($third-row-left-odd - 50%) * 0.4)+50%;

//angle
$third-row-top: 30% - $move-up;
$second-row-top: $third-row-top+$angle * 4%;
$front-top: $second-row-top+$angle * 1%;
$fourth-row-top: $third-row-top - $angle * 2.8%;
$back-top: $fourth-row-top - $angle * 0.8%;
//---------------------------------------------------------------------------------------

.blocker {
    top: 35% - $move-up;
    left: 50%;
    transform: translateX(-50%);
    width: 22.3%;
    height: 46%;
    background: grey;
    opacity: 0.3;
    border-radius: 52%;
}

//Classes depend on how many items

