#menu {
	.si-menu {
		position: absolute;
		top: 35%;
		width: 16%;
		height: 30%;
		transition: transform 0.14s linear;

		&.menu1 {
			left: 14%;
		}

		&.menu2 {
			@include h-center;
			left: 4%;
		}

		&.menu3 {
			left: 74%;
		}

		img,
		svg {
			@include full-size;

			&.start {
				opacity: 1;
			}

			&.hover {
				opacity: 0;
			}

			&.check {
				top: -8%;
				left: -1%;
				width: 28%;
				height: 25%;
				opacity: 0;
			}
		}

		&:not([disabled]):hover {
			transform: scale(1.6);

			.start {
				opacity: 0;
			}

			.hover {
				opacity: 1;
			}
		}

		&.completed {
			.start {
				opacity: 0;
			}

			.hover {
				opacity: 0.5;
			}

			.check {
				opacity: 1;
			}
		}

		&.unlocked {
			animation: pulse 1s infinite alternate;
		}
	}
}

@keyframes pulse {
	0% {
		transform: scale(0.95);
	}

	100% {
		transform: scale(1);
	}
}
