.noEvents {
	pointer-events: none;
}

.si-guidelines {
	@include full-size;
	z-index: -3;
}

.si-vignette {
	@include over-size;
	z-index: -4;
}

/* --------- Project Specific Classes, Change these for each project -------  */

section {
	h2 {
		position: absolute;
		top: 2.5%;
		left: 7%;
		font-size: 1.3rem;
		text-align: left;
		text-decoration: underline;
		text-transform: uppercase;
	}

	h3 {
		position: absolute;
		top: 10%;
		left: 7%;
		width: 80%;
		font-size: 1rem;
		text-align: left;
	}
}

/* ------- Your Styling ---------- */
#start {
	h1 {
		@include h-center;
		top: 30%;
		width: 66%;
		font-size: 1.4rem;
		text-align: center;
	}

	.si-course-logo {
		@include h-center;
		top: 6%;
		width: auto;
		max-width: 60%;
		height: 22%;
	}

	.si-start-img {
		@include h-center;
		top: 46%;
		width: auto;
		max-width: 40%;
		height: 36%;
	}
}

#i1 {
	.draggable {
		position: absolute;
		top: 40%;
		left: 15%;
		width: 20%;
		height: 20%;

		img {
			@include full-size;
		}
	}

	.droppable {
		position: absolute;
		top: 40%;
		left: 65%;
		width: 20%;
		height: 20%;

		img {
			@include full-size;
		}
	}
}

.i1-click-position {
	top: 55%;
	left: 30%;
}

#i3 {
	.hotspot-button {
		@include v-center;
		width: 20%;
		height: 20%;
		box-sizing: border-box;
		border: 0.1rem solid black;
		background-color: white;
		box-shadow: -0.1rem 0.1rem 0.15rem 0 rgba($color: #000000, $alpha: 0.45);

		&.hotspot-style-1 {
			left: 10%;
		}

		&.hotspot-style-2 {
			left: 40%;
		}

		&.hotspot-style-3 {
			left: 70%;
		}
	}
}

#end {
	h1 {
		@include h-center;
		top: 7%;
		width: 66%;
		font-size: 2rem;
		font-weight: 600;
		text-align: center;
	}

	h2 {
		@include h-center;
		top: 20%;
		width: 66%;
		font-size: 1.6rem;
		font-weight: 500;
		text-align: center;
		text-decoration: none;
		text-transform: none;
	}

	.center-image {
		@include center;
		top: 58%;
		height: 42%;
	}

	.fireworks {
		@include v-center;
		top: 58%;
		height: 42%;
	}

	.left {
		left: 16%;
	}

	.right {
		right: 16%;
	}

	.si-end-logo {
		@include h-center;
		bottom: 2%;
		width: 23%;
	}
}
