 @charset "UTF-8";


 /*----VIDEOJS----*/
 .vjs-button:hover,
 .vjs-button:focus {
	 color: $si-main-color;
	 
 }

 .video-js .vjs-play-progress:before,
 .video-js .vjs-volume-level:before,
 .vjs-icon-circle:before {
 	color: $si-main-color;
 }

 .video-js .vjs-play-progress {
 	background-color: $si-main-color !important;
 }

 .video-js .vjs-volume-level {
 	background-color: $si-main-color !important;
 }

 .vjs-big-play-button:hover,
 .vjs-big-play-button:focus {
 	color: $si-main-color;
 }

 .video-js .vjs-load-progress div {
 	background: rgba(184, 193, 207, .75) !important;
 }

/* removes video.js fullscreen button; do not add this, it breaks the course */
.vjs-fullscreen-control {
	display: none !important;
}

/* removes video.js cc settings button; cc settings have no styling so caveat emptor */
.vjs-texttrack-settings {
	display: none;
}



.video-js.vjs-default-skin.vjs-paused .vjs-big-play-button {
	display: block !important;
	font-size: 2em;
}

iframe {
	overflow: hidden;
}

.video-js {
	overflow: hidden;
	background-color: white;
	font-size: 10px;
	outline: 0 !important;
}

video div {
	font-size: 10px;
}

.vjs-poster {
	border-right: 1px solid transparent;
	border-left: 1px solid transparent;
	background-color: white;
	outline: 0 !important;
}

.vjs-menu li.vjs-selected,
.vjs-menu li.vjs-selected:focus,
.vjs-menu li.vjs-selected:hover {
	background-color: lightgray;
}

.vjs-resolution-button {
	color: #fff;
}

.video-js .vjs-big-play-button {
	background-color: rgba(255, 153, 0, .0);
}

.video-js .vjs-progress-control {
	cursor: default;
}

:focus {
	outline: none !important;
}

.video-js {
	color: black;
}

.video-js .vjs-big-play-button {
	border: none;
}

.video-js .vjs-big-play-button:focus, .video-js:hover .vjs-big-play-button {
	background-color: rgba(255, 153, 0, 0);
}

.video-js .vjs-control-bar {
	background-color: rgba(255, 153, 0, 0);
}

.video-js .vjs-captions-button .vjs-icon-placeholder:before, .video-js:lang(en) .vjs-subs-caps-button .vjs-icon-placeholder:before, .video-js:lang(fr-CA) .vjs-subs-caps-button .vjs-icon-placeholder:before, .vjs-icon-captions:before {
	content: "G";
}

.vjs-resolution-button .vjs-icon-placeholder:before {
	content: 'F';
}

.vjs-menu .vjs-menu-content > * {
	color: black;
}

.video-js .vjs-subs-caps-button + .vjs-menu .vjs-captions-menu-item .vjs-menu-item-text .vjs-icon-placeholder:before {
	content: "\f10d";
	font-family: VideoJS;
	font-size: inherit;
	line-height: inherit;
}

.vjs-menu-button-popup .vjs-menu .vjs-menu-content {
	background: none;
}

/* VERTICAL VOLUME BUTTON */
.video-js .vjs-volume-vertical {
    bottom: 6.5em;
    background-color: rgba(0,0,0,0);
}

/* REMOVE POINTER CURSOR AND HANDLE IN CASE PROGRESS CONTROLL IS DISABLED */
.vjs-progress-holder.disabled {
	cursor: default;

	.vjs-play-progress:before{
		display: none;
	}
}
