.si-modal-container {
	@include full-size;
	z-index: 100;
	display: none;
	background-color: rgba(0, 0, 0, 0.4);
	cursor: pointer;
	opacity: 0;
	pointer-events: none;
	transition: opacity 0.1s linear;

	&.si-modal-container-open {
		display: inline-block;
		opacity: 1;
		pointer-events: all;
		transition: opacity 0.14s linear;
	}

	&.si-hover-hotspot {
		// position: static;
		pointer-events: none;

		.si-modal {
			pointer-events: none;
		}
	}

	.si-modal {
		@include center;
		display: none;
		width: 60%;
		height: 60%;
		border: 0.1rem solid black !important;
		background: #ffffff;
		box-shadow: 0.2rem 0.2rem 0.3rem 0px rgba(0, 0, 0, 0.5);
		opacity: 0;

		&.si-current-modal {
			display: inline-block;
			opacity: 1;
			transition: opacity 0.14s linear;
		}

		.si-modal-closer {
			position: absolute;
			z-index: 99;
			top: 0.3rem;
			right: 0.3rem;
			width: 1rem;
			height: 1rem;

			img,
			svg {
				@include full-size;
			}

			&:not([disabled]):hover {
				fill: $si-main-color;
				stroke: $si-main-color;

				line,
				path {
					fill: $si-main-color;
					stroke: $si-main-color;
				}
			}

			&:not([disabled]):focus {
				fill: $si-main-color;
				stroke: $si-main-color;

				line,
				path {
					fill: $si-main-color;
					stroke: $si-main-color;
				}
			}
		}

		.si-modal-content-container {
			@include center;
			width: 90%;
			height: 96%;

			p {
				@include text($size: 1rem, $weight: 500);

				b {
					font-size: 1.3rem;
					font-weight: 600;
				}
			}

			&.side-by-side {
				p {
					@include v-center;
					left: 0%;
					width: 60%;
					text-align: left;
				}

				img {
					@include v-center;
					left: 63%;
					width: 37%;
					max-height: 78%;
				}
			}

			&.top-to-bottom {
				p {
					@include h-center;
					top: 0%;
					width: 100%;
				}

				img {
					@include center;
					top: 65%;
					max-width: 100%;
					height: 60%;
				}
			}
		}
	}
}
