body#interactive {
	h3.server-name {
		position: absolute;
		top: -1.4em;
		left: 0;
		color: #f90;
		font-family: Montserrat, sans-serif;
		font-size: 32px;
		font-weight: 700;
	}

	h2,
	h3 {
		left: 4rem;
	}

	h2 {
		font-size: 1.6rem;
		font-weight: 400;
	}

	h3 {
		top: 11%;
		font-size: 1.2rem;
	}

	.si-quiz {
		width: auto;

		.si-quiz-answer p {
			width: auto;
			font-size: 1rem;
		}
	}

	#guidelines {
		position: absolute;
		z-index: 100;
		width: 100%;
		height: 100%;

		.guidelines {
			position: absolute;
			width: 100%;
			height: 100%;
		}

		.guidelines-1 {
			background-image: url('../media/imgs/_imgs/grid2.svg');
			background-size: 1rem;
			opacity: 0.12;
		}

		.guidelines-2 {
			background-image: url('../media/imgs/_imgs/grid2.svg');
			background-size: 0.5rem;
			opacity: 0.1;
		}
	}

	#Preloader {
		p {
			top: 77%;
			font-weight: 300;
		}

		.preloader-wrapper {
			top: 42%;
			text-align: center;
		}
	}

	#si-nav-container #si-course-nav button {
		border: none;
	}

	#si-nav-container #si-lower-nav {
		#si-clock {
			.si-nav-symbol {
				top: 49%;
			}

			div {
				top: 46%;
				margin-left: 130%;
			}
		}

		#si-progress #si-progress-meter .progressbar-text {
			margin-left: 164%;
			white-space: nowrap;
		}
	}

	#si-nav-container #si-buttons #si-start {
		top: unset;
		right: 0;
		bottom: 0;
		left: initial;
		margin: 0 7% 4% 0;
	}

	#si-logo {
		position: absolute;
		top: 1rem;
		right: 1.2rem;
		left: auto;
		width: 20%;
		height: auto;

		img {
			position: static;
		}
	}

	.si-modal-container {
		.si-modal {
			height: 64%;
			border: none !important;
			background-color: transparent;
			background-position: center;
			background-repeat: no-repeat;
			background-size: cover;
			box-shadow: none;

			&.si-feedback-positive {
				background-image: url('../media/project/imgs/feedback-positive.svg');
			}

			&.si-feedback-negative {
				background-image: url('../media/project/imgs/feedback-negative.svg');
			}

			&.si-feedback-negative-final {
				background-image: url('../media/project/imgs/feedback-negative-final.svg');
			}

			.si-modal-content-container {
				height: auto;
				padding-right: 45%;
				padding-bottom: 0.5rem;
				padding-left: 5%;
				text-align: left;

				p {
					margin: 1rem 0;
				}
			}
		}

		img {
			position: relative;
		}
	}

	.si-modal-closer {
		position: absolute;
		z-index: 99;
		top: 0.4rem;
		right: -0.4rem;
		width: 1rem;
		height: 1rem;
		padding: 0.5rem;
		border: solid 3px;
		border-radius: 50%;
		background-color: white;
		pointer-events: none;

		path {
			stroke: black;
			stroke-width: 2px;
		}
	}

	.reveal {
		overflow: visible;
		transform: scale(0.9);
		transform-origin: bottom;

		.slides {
			overflow: hidden;
			border-radius: 1rem;
			outline: dashed 2px #f90;
			text-align: start;

			section {
				overflow: hidden;
				border-radius: 1rem;

				h2 {
					text-decoration: none;
				}
			}
		}

		.backgrounds {
			pointer-events: none;
		}
	}

	.circle-image {
		top: 5rem;
		right: 3rem;
		width: 17rem;
	}

	.whiteboard {
		top: 4.9rem;
		right: 0;
		left: 0;
		width: 100%;
		height: 17rem;
	}

	#start {
		padding: 12% 7% 5% 0;

		header {
			position: relative;

			// width: 100%;
			height: 100%;

			// outline: dashed 1px firebrick;
			// padding-top: 12%;
			// padding-right: 7%;
			text-align: right;

			img,
			h1 {
				position: static;
			}

			h1 {
				top: 20%;
				right: 5%;
				left: auto;
				width: 100%;
				margin-bottom: 1rem;
				font-size: 1.6rem;
				text-align: right;
			}

			img {
				display: block;
				width: 55%;
				max-width: 100%;
				height: auto;
				margin-bottom: 1rem;
				margin-left: auto;
			}
		}

		button {
			position: absolute;
			right: 0;
			bottom: 0;
			display: block;
			padding: 0.3rem 0.9rem;
			border: 0.12rem solid black;
			border-radius: 0.15rem;

			// margin: 0 7% 1.8rem auto;
			margin-left: auto;
			background-color: #f90;
			box-shadow: -0.09rem 0.09rem 0.1rem rgb(0 0 0 / 0.4);
			color: white;
			cursor: pointer;
			font-size: 0.9rem;
			text-transform: uppercase;
			transition: transform 0.12s linear;

			&:hover {
				transform: scale(0.96);
			}

			&::after {
				position: relative;
				top: 0;
				left: 0.18rem;
				display: inline-block;
				width: 0.59rem;
				height: 0.59rem;
				border-radius: 0.135rem;
				border-top: solid transparent 0.135rem;
				border-right: solid white 0.135rem;
				border-bottom: solid white 0.135rem;
				border-left: solid transparent 0.135rem;
				content: '';
				transform: rotate(-45deg);
				vertical-align: baseline;
			}
		}

		.maria-start {
			top: 1rem;
			left: 1rem;
			width: 22rem;
		}
	}

	#intro {
		.maria {
			top: 5rem;
			right: 5rem;
			width: 12rem;
		}

		.intro-text {
			position: relative;

			// width: 100%;
			// height: 100%;
			top: 16%;

			// padding-top: 12%;
			// padding-right: 7%;
			padding-left: 4rem;

			// outline: dashed 1px firebrick;
			text-align: right;

			img,
			h1,
			h3,
			u,
			div {
				position: static;
			}

			.indent {
				position: relative;
				left: 1rem;
			}

			h1 {
				// top: 20%;
				// right: 5%;
				// left: auto;
				// width: 100%;
				margin-bottom: 1rem;
				font-size: 1.6rem;
				text-align: left;
			}

			h3 {
				left: 0;
				margin-bottom: 0.2rem;
				font-size: 1rem;
			}

			p {
				margin-bottom: 1rem;
				text-align: left;
			}

			img {
				display: block;
				width: 55%;
				max-width: 100%;
				height: auto;
				margin-bottom: 1rem;
				margin-left: auto;
			}
		}
	}

	#i3 {
		.the-text {
			position: relative;
			top: 28%;
			display: block;
			width: 50%;
			height: 35%;
			margin: 0 auto;
			font-size: 1.3rem;
			line-height: 2.5rem;
			text-align: center;
		}

		.the-blank {
			display: inline-block;
			width: 6rem;

			// height: 1rem;
			// padding-bottom: 0.1rem;
			border-bottom: solid 1px black;

			// margin: 0 0.3rem;
			line-height: 2rem;
		}

		.droppable {
			position: absolute;
			top: 25%;
			left: 34.4%;
			width: 10rem;
			height: 4rem;

			// outline: solid;
		}

		.draggable {
			position: absolute;
			width: 10rem;
			height: 4rem;
			padding: 1rem;

			// outline: solid 1px;
			text-align: center;

			// transition: transform 0.3s ease-in-out;
			user-select: none;

			&.dropped {
				// background-color: firebrick;
				cursor: default;
			}

			img {
				position: relative;
			}
		}

		#cube-2 {
			top: 0;
			left: 30rem;
			background-color: teal;
		}

		#i3_drag1 {
			top: 67%;
			left: 30%;
			transform: rotate(-5deg);

			&.ui-draggable-dragging {
				transform: rotate(-3deg);
			}

			&.dropped {
				transform: rotate(-2deg);
			}
		}

		#i3_drag2 {
			top: 61%;
			left: 52%;
			transform: rotate(12deg);

			&.ui-draggable-dragging {
				transform: rotate(7deg);
			}

			&.dropped {
				transform: rotate(2deg);
			}
		}

		#i3_drag3 {
			left: 65%;
		}
	}

	#end {
		h1,
		h2 {
			width: 100%;
			text-align: left;
		}

		.maria {
			top: 1rem;
			left: 2rem;
			width: 18rem;
		}

		.text-block {
			position: relative;
			top: 8%;
			right: 0;
			width: 60%;
			padding-right: 2rem;
			margin-left: auto;

			// width: 100%;
			// height: 100%;
			// padding-top: 12%;
			// padding-right: 7%;
			// outline: dashed 1px firebrick;
			text-align: right;

			img,
			h1,
			h2,
			h3,
			u,
			p,
			div {
				position: static;
				text-align: right;
			}

			.indent {
				position: relative;
				left: 1rem;
			}

			h1 {
				// margin-bottom: 1rem;
				font-size: 2rem;
				font-weight: 400;
				text-align: right;
				text-transform: uppercase;
			}

			h2 {
				margin-bottom: 2rem;
			}

			h3 {
				width: 100%;
				margin-top: 2rem;
				margin-bottom: 0.2rem;
				margin-left: auto;
				font-size: 1.2rem;
				font-weight: 400;
			}

			p {
				width: 80%;
				margin-bottom: 0.7rem;
				margin-left: auto;
				line-height: 1.4;
				text-align: right;
			}

			a {
				color: #f90;
				font-weight: 400;
				text-decoration: underline;
				text-shadow: 0 0 black;
			}
		}
	}
}

.cube {
	position: absolute;
	display: inline-block;
	width: 10rem;
	height: 10rem;
}

#cube-1 {
	top: 0;
	left: 0;
	background-color: firebrick;
}

#cube-3 {
	top: 0;
	left: 20rem;
	background-color: orange;
}
