/* -------------------------------- Hotspot basic styling -------------------------------------- */
.hotspot-modal-blocker {
	position: absolute;
	z-index: 99 !important;
	top: 0%;
	left: 0%;
	display: none;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.4);
	cursor: pointer;

	.modal-style {
		@include center;
		position: absolute;
		display: none;
		width: 80%;
		height: 80%;
		box-sizing: border-box;
		border: 0.2rem solid black !important;
		background: #ffffff;
		box-shadow: 0.2rem 0.2rem 0.3rem 0px rgba(0, 0, 0, 0.5);
		cursor: auto;

		.modal-close {
			position: absolute;
			top: 0.2rem;
			right: 0.2rem;
			width: 1.1rem;
			height: 1rem;
			cursor: pointer;
			font-size: 2.1rem;
			line-height: 1.1rem;

			svg {
				@include full-size;
			}

			&:hover {
				color: $si-main-color;
			}
		}

		&.modal-click-anywhere {
			cursor: pointer;

			.modal-close {
				display: none;
			}
		}
	}

	&.hover-hotspot {
		position: static;
		background: transparent;

		.modal-style {
			pointer-events: none;
		}
	}
}

.hotspot-button {
	&.active-hotspot,
	&.selected-hotspot {
		background: $si-main-color;
	}
}
