$si-bg-00: '../media/imgs/background/none.svg';
$si-bg-01: '../media/imgs/background/paper.png';
$si-bg-02: '../media/imgs/background/vignette.png';
$si-bg-03: '../media/imgs/background/trees.png';
$si-bg-04: '../media/imgs/background/white-abstract.png';
$si-bg-05: '../media/imgs/background/fondo-tecnologia-abstracto-oscuro.png';
$si-bg-06: '../media/imgs/background/banniere-abstraite-motif-pointille-orange-noir.png';

body#interactive {
	&.si-bg-full {
		background: url($si-bg-01);
		background-size: 100% 100%;
	}

	&.si-bg-slides section {
		background: url($si-bg-01);
		background-size: 100% 100%;
	}
}
