$si-nav-map: (
	'icon-color': white,
	'button-text-color': white,
	'button-font-size': 0.9rem,
	'button-font-weight': 500,
	'submit-reset-text-position': -8%,
	'start-text-position': -3%,
	'button-icon-position': 75%,
	'background-color': $si-main-color,
	'clock-width': 400%,
	'progress-meter-color-classic': $si-main-color,
	'classic-fs': 1rem,
	'classic-icon-position': 70%
);

#si-nav-container {
	@include full-size;
	z-index: 11;

	svg {
		@include full-size;
	}

	button {
		backface-visibility: hidden;
		background-color: rgba(0, 0, 0, 0);
		pointer-events: all;

		@include basic-transition;
		@include scale-hover;
		@include scale-active;
	}

	// &.feedback-open {
	//     button {
	//         z-index: 11;
	//     }

	//     #si-click-icon{
	//         z-index: 11;
	//     }
	// }

	&.vjs-fade-out {
		button {
			opacity: 0;
			transition: opacity 0.8s linear;
		}
	}
	// Top Left Controls (Home, Menu, Full)
	#si-course-nav {
		position: absolute;
		top: 3.5%;
		left: 1.75%;
		display: block;
		width: 2.5%;
		height: auto;
		height: 98%;

		button {
			position: relative;
			top: 0%;
			left: 0%;
			display: block;
			width: 100%;
			height: 4%;
			margin-bottom: 20%;
		}

		#si-home.si-nav-style-clean {
			#detail-3,
			#detail-4 {
				fill: $si-main-color;
			}
		}

		.si-nav-style-clean {
			line,
			polyline {
				stroke: $si-main-color;
			}
		}

		.si-nav-style-custom {
			polygon {
				fill: $si-main-color;
			}

			line,
			polyline {
				stroke: $si-main-color;
			}
		}
	}
	// Slide Controls (Back, Next, Exit, Replay)
	#si-slide-nav {
		@include full-size;

		button {
			position: absolute;
			top: 46.5%;
			width: 4%;

			@include ratio-height(4%);
		}

		#si-back,
		#si-replay {
			left: 1%;
		}

		#si-next,
		#si-exit {
			right: 1%;
		}

		.si-nav-style-classic {
			border: 0.12rem solid black;
			background-color: black;
			border-radius: 50%;
			box-shadow: inset 0.08rem 0.08rem 0 0.04rem white, -0.09rem 0.09rem 0.1rem rgba(0, 0, 0, 0.4);
		}

		.si-nav-style-clean {
			border: 0.12rem solid black;
			background-color: map-get($si-nav-map, 'background-color');
			border-radius: 50%;
			box-shadow: -0.09rem 0.09rem 0.1rem rgba(0, 0, 0, 0.4);

			&#si-next,
			&#si-back,
			&#si-replay,
			&#si-exit {
				svg path {
					fill: map-get($si-nav-map, 'icon-color');
				}
			}
		}

		.si-nav-style-custom {
			background-color: map-get($si-nav-map, 'background-color');
			border-radius: 50%;
			box-shadow: -0.09rem 0.09rem 0.1rem rgba(0, 0, 0, 0.4);

			&#si-next,
			&#si-back {
				svg polyline {
					stroke: map-get($si-nav-map, 'icon-color');
				}
			}

			&#si-exit,
			&#si-replay {
				svg rect {
					fill: map-get($si-nav-map, 'icon-color');
				}

				svg polyline {
					stroke: map-get($si-nav-map, 'icon-color');
				}

				svg path {
					fill: map-get($si-nav-map, 'icon-color');
					stroke: map-get($si-nav-map, 'icon-color');
				}
			}
		}
	}
	// progress meter and clock
	#si-lower-nav {
		#si-progress {
			position: absolute;
			top: 91.5%;
			left: 1.2%;
			width: 3.5%;

			@include ratio-height(3.5%);

			#si-progress-meter {
				position: absolute;
				width: 70%;
				height: 70%;

				@include center();

				svg {
					position: 'absolute';
					left: '0%';
					width: 'auto';
					height: '100%';

					path:first-child {
						stroke-width: 6;
					}

					path:nth-child(2) {
						stroke-width: 9 !important;
					}
				}

				.progressbar-text {
					position: absolute;
					left: 0%;
					margin-left: 150%;
					color: black !important;
					font-size: 1rem;
					font-weight: 500;

					@include v-center();
				}
			}

			&.si-nav-style-classic {
				svg {
					path:first-child {
						stroke: white;
					}

					path:nth-child(2) {
						color: map-get($si-nav-map, 'progress-meter-color-classic');
						stroke: map-get($si-nav-map, 'progress-meter-color-classic');
					}
				}
			}

			&.si-nav-style-clean {
				svg {
					path:first-child {
						stroke: black;
					}

					path:nth-child(2) {
						color: white;
						stroke: white;
					}
				}
			}

			&.si-nav-style-custom {
				svg {
					path:first-child {
						stroke: black;
					}

					path:nth-child(2) {
						color: white;
						stroke: white;
					}
				}
			}
		}

		#si-clock {
			position: absolute;
			top: 91.5%;
			left: 1.2%;
			width: 3.5%;

			@include ratio-height(3.5%);

			div {
				position: absolute;
				left: 0%;
				width: map-get($si-nav-map, 'clock-width');
				margin-left: 120%;
				color: black !important;
				font-size: 1rem;
				font-weight: 500;
				text-align: left;

				@include v-center();
			}
		}

		.si-nav-style-classic {
			border: 0.12rem solid black;
			background-color: black;
			border-radius: 50%;
			box-shadow: inset 0.06rem 0.06rem 0 0.01rem white;
		}

		.si-nav-style-clean {
			border: 0.12rem solid black;
			background-color: map-get($si-nav-map, 'background-color');
			border-radius: 50%;
		}

		.si-nav-style-custom {
			background-color: map-get($si-nav-map, 'background-color');
			border-radius: 50%;
		}
	}
	// top right logo
	#si-logo {
		position: absolute;
		top: 1.5%;
		left: 89.5%;
		width: 9.5%;
		height: 10%;

		img {
			@include full-size();
		}
	}
	// start, submit, reset, custom-submit
	#si-buttons {
		button {
			position: absolute;

			top: 90.5%;
			width: 10%;
			height: 7.5%;
			backface-visibility: hidden;
		}

		div {
			width: 81%;
			font-size: map-get($si-nav-map, 'button-font-size');
			font-weight: map-get($si-nav-map, 'button-font-weight');
			pointer-events: none;
			text-align: center;

			@include center();
		}

		svg.si-button-arrow {
			left: map-get($si-nav-map, 'button-icon-position');
			width: 22%;
			height: 53%;

			@include v-center;
		}

		#si-start {
			@include h-center();

			.si-classic-rect-image {
				@include full-size;
			}

			div {
				left: map-get($si-nav-map, 'start-text-position');
			}
		}

		.si-submit-style {
			left: 85%;

			div {
				left: map-get($si-nav-map, 'submit-reset-text-position');
			}
		}

		#si-reset {
			left: 74%;

			div {
				left: map-get($si-nav-map, 'submit-reset-text-position');
			}
		}

		.si-nav-style-classic {
			width: 11%;
			height: 9.5%;

			&#si-start,
			&#si-reset,
			&.si-submit-style {
				div {
					top: 45%;
					color: white;
					font-size: map-get($si-nav-map, 'classic-fs');
				}

				svg.si-button-arrow {
					left: map-get($si-nav-map, 'classic-icon-position');
				}
			}
		}

		.si-nav-style-clean {
			div {
				color: map-get($si-nav-map, 'button-text-color');
				text-transform: uppercase;
			}
			border: 0.12rem solid black;
			background-color: map-get($si-nav-map, 'background-color');
			border-radius: 0.15rem;
			box-shadow: -0.09rem 0.09rem 0.1rem rgba(0, 0, 0, 0.4);

			img.si-classic-rect-image {
				display: none;
				opacity: 0;
			}

			svg path {
				fill: map-get($si-nav-map, 'icon-color');
			}
		}

		.si-nav-style-custom {
			div {
				color: map-get($si-nav-map, 'button-text-color');
				text-transform: uppercase;
			}

			background-color: map-get($si-nav-map, 'background-color');
			box-shadow: -0.09rem 0.09rem 0.1rem rgba(0, 0, 0, 0.4);

			.si-classic-rect-image {
				display: none;
				opacity: 0;
			}

			&#si-reset {
				svg polyline {
					stroke: map-get($si-nav-map, 'icon-color');
				}

				svg path {
					fill: map-get($si-nav-map, 'icon-color');
					stroke: map-get($si-nav-map, 'icon-color');
				}
			}

			&.si-submit-style,
			&#si-start {
				svg polyline {
					stroke: map-get($si-nav-map, 'icon-color');
				}
			}
		}
	}
	// all icons
	#si-click-icon {
		position: absolute;
		width: 2%;
		height: 4%;

		svg.si-nav-style-clean {
			#classic_click_button_Image :last-child {
				fill: map-get($si-nav-map, 'background-color');
			}
		}

		svg.si-nav-style-custom {
			#detail,
			#detail-2 {
				fill: map-get($si-nav-map, 'background-color');
			}
		}

		&.si-click-icon {
			animation: click 1.8s ease-out infinite;
		}

		&.si-drag-up-icon {
			animation: drag-up 1.8s ease-out infinite;
		}

		&.si-drag-down-icon {
			animation: drag-down 1.8s ease-out infinite;
		}

		&.si-drag-left-icon {
			animation: drag-left 1.8s ease-out infinite;
		}

		&.si-drag-right-icon {
			animation: drag-right 1.8s ease-out infinite;
		}
	}
	// SVG symbols inside of button
	.si-nav-symbol {
		@include center;
		width: 60%;
		height: 60%;
	}
}

// NO MORE FLASHING OF THE BIG BLACK CLASSIC BUTTONS WHEN THE PRELOADER IS HIDDEN TOO SOON
.si-nav__js {
	display: none;
}

@keyframes click {
	0% {
		transform: scale(1);
	}

	50% {
		transform: scale(0.9);
	}

	100% {
		transform: scale(1);
	}
}

@keyframes drag-up {
	10% {
		transform: scale(0.95);
	}

	65% {
		transform: scale(0.95) translateX(0%) translateY(-80%);
	}

	66% {
		transform: scale(1) translateX(0%) translateY(-80%);
	}

	100% {
		transform: translateX(0%) translateY(0%);
	}
}

@keyframes drag-down {
	10% {
		transform: scale(0.95);
	}

	65% {
		transform: scale(0.95) translateX(0%) translateY(80%);
	}

	66% {
		transform: scale(1) translateX(0%) translateY(80%);
	}

	100% {
		transform: translateX(0%) translateY(0%);
	}
}

@keyframes drag-left {
	10% {
		transform: scale(0.95);
	}

	65% {
		transform: scale(0.95) translateX(-80%) translateY(0%);
	}

	66% {
		transform: scale(1) translateX(-80%) translateY(0%);
	}

	100% {
		transform: translateX(0%) translateY(0%);
	}
}

@keyframes drag-right {
	10% {
		transform: scale(0.95);
	}

	65% {
		transform: scale(0.95) translateX(80%) translateY(0%);
	}

	66% {
		transform: scale(1) translateX(80%) translateY(0%);
	}

	100% {
		transform: translateX(0%) translateY(0%);
	}
}
