#si-side-menu-container {
    // cursor: pointer;
    z-index: 15;

    &.menu-open {
        pointer-events: all;
        background-color: rgba(0, 0, 0, .4);
    }
    
    $text-colored-bg: white;
    $font-scale: 1.1rem;
    $side-menu-width: 16%;

    @include full-size;
    @include basic-transition;

    @mixin header-font($scale) {
        $size: $scale * 1;
        font-size: $size
    }

    @mixin section-font($scale) {
        $size: $scale * .8;
        font-size: $size
    }

    @mixin navigation-font($scale) {
        $size: $scale * .7;
        font-size: $size
    }


    #si-side-menu {
        position: absolute;
        height: 100%;
        width: $side-menu-width;
        left: -($side-menu-width + 4%);
        top: 0;
        z-index: 30;
        background-color: rgba(255, 255, 255, 0.96);
        box-shadow: .1rem 0 .15rem rgba(0, 0, 0, 0.4);
        overflow-x: hidden;
        transition: 0.6s;
        pointer-events: all;

        &.menu-open {
            left: 0;
        }

        .si-menu-closer {
            position: absolute;
            top: 1%;
            right: 1%;
            width: 11%;
            height: 3%;
            z-index: 1;
            @include basic-transition;
            &:not([disabled]):hover{
                @include scale-hover;
            }

            svg {
                @include full-size;
            }
        }

        .si-menu-header {
            position: relative;
            width: 100%;
            height: 20%;
            background-color: $si-main-color;

            p {
                @include center;
                color: $text-colored-bg;
                @include header-font($font-scale);
                font-weight: 600;
            }
        }

        .si-menu-section {
            position: relative;
            height: 5%;
            width: auto;
            border-bottom: .01rem solid rgba(82, 82, 82, 0.952) !important;
            margin: 6% 10% !important;

            p {
                @include section-font($font-scale);
                font-weight: 600;
            }
        }

        .si-menu-navigation {
            position: relative;
            width: 100%;
            height: 70%;
            $svg-width: 70%;
            $svg-height: 60%;

            button {
                position: relative;
                width: 100%;
                height: 10%;
                @include basic-transition;
                svg.completed {
                    opacity: 1 !important;
                }
            }

            .si-menu-checkbox {

                position: absolute;
                top: 0;
                left: 0;
                margin-left: 4%;
                height: 100%;
                width: 20%;

                svg.si-menu-box-style {
                    @include center;
                    height: $svg-height;
                    width: $svg-width;
                }

                svg.si-menu-check-style {
                    @include v-center;
                    height: $svg-height;
                    width: $svg-width;
                    top: 39%;
                    left: 33%;
                    opacity: 0;
                }
            }

            button.si-nav-style-classic {
                &:not([disabled]):hover {
                    background-color: $si-main-color;
                    color: $text-colored-bg;

                    svg {
                        // stroke-width: 0;
                        fill: $text-colored-bg;
                    }
                }

                svg.si-menu-check-style {
                    left: 27%;
                }
            }

            button.si-nav-style-clean {
                &:not([disabled]):hover {
                    background-color: $si-main-color;
                    color: $text-colored-bg;

                    svg {
                        path {
                            fill: $text-colored-bg;
                        }
                    }
                }

            }

            button.si-nav-style-custom {
                &:not([disabled]):hover {
                    background-color: $si-main-color;
                    color: $text-colored-bg;

                    svg {

                        rect,
                        polyline {
                            stroke: $text-colored-bg;
                        }
                    }
                }

            }



            .si-menu-chapters {
                @include v-center;
                @include navigation-font($font-scale);
                background-color: rgba(0, 0, 0, 0);
                text-align: left;
                left: 27%;
                width: 71%;
            }
        }
    }

    
}

