.form-style {
	input {
		&[type='submit'] {
			display: none !important;
		}

		&.separated {
			width: 1rem;
		}
	}

	.radio-container {
		cursor: pointer;

		input[type='radio'] {
			cursor: pointer;

			&.hide-radio {
				width: 0;
				height: 0;
				opacity: 0;
			}

			//X-Style
			&:checked ~ span.x-style {
				// background-color: #2196f3;
			}

			& ~ span.x-style {
				display: inline-block;
				width: 1rem;
				height: 1rem;
				cursor: pointer;
				vertical-align: text-top;
			}

			& ~ span.x-style:after {
				position: relative;
				display: none;
				width: 100%;
				color: map-get($map: $si-color-map, $key: lilly);
				content: 'X';
				text-align: center;
			}

			&:checked ~ span.x-style:after {
				display: block;
			}

			//Dot-Style
			&:checked ~ span.dot-style {
				// background-color: #2196f3;
			}

			& ~ span.dot-style {
				display: inline-block;
				width: 1rem;
				height: 1rem;
				box-sizing: border-box;
				border: 0.1rem solid black;
				border-radius: 50%;
				cursor: pointer;
				vertical-align: text-top;
			}

			& ~ span.dot-style:after {
				@include center;
				position: relative;
				// top: -0.2rem;
				display: none;
				width: 0.5rem;
				height: 0.5rem;
				background: map-get($map: $si-color-map, $key: lilly);
				border-radius: 50%;
				color: map-get($map: $si-color-map, $key: lilly);
				content: ' ';
				text-align: center;
			}

			&:checked ~ span.dot-style:after {
				display: block;
			}
		}
		//X-Style
		&:hover input ~ span.x-style:after {
			display: block;
		}
		//Dot-Style
		&:hover input ~ span.dot-style:after {
			display: block;
		}
	}

	/* Show the checkmark when checked */
}
