$si-yanone: 'YanoneKaffeesatz';
$si-patrick: 'PatrickHand';
$si-simpleshow: 'simpleshow';
$si-lilly: "din-2014", Arial, Helvetica, sans-serif;
$si-color-map: (
    "simpleshow": #ff9900,
    "lilly": #d52b1e,
    "merck": #00877c,
    "bms": #b0c164,
    "ocean": #01acac,
    "vic" : rgba(3, 148, 153, 0.1)
);
$si-main-color: map-get($si-color-map, "simpleshow");
$si-main-font: $si-yanone;


