#Preloader {
	position: absolute;
	z-index: 99999;
	width: 100%;
	height: 100%;
	background: url('../media/imgs/background/paper.png');
	background-color: #fff;
	background-size: 100% 100%;
	color: black;
	cursor: pointer;
	font-size: 1.2rem;
	font-weight: 400;
	pointer-events: all;

	p {
		position: absolute;
		top: 80%;
		width: 100%;
		text-align: center;
	}

	#preloader_text_2 {
		display: none;
	}

	.preloader-wrapper {
		@include center;

		height: 46%;

		img,
		svg {
			position: relative;
			height: 100%;
			animation: scaleUpAndDown 3s infinite ease-in-out;
			transform-origin: center;
		}

		svg .st0 {
			fill: black;
		}
	}
}

@keyframes scaleUpAndDown {
	0%,
	100% {
		transform: scale(1);
	}

	50% {
		transform: scale(1.08);
	}
}

// This makes sure that SVG images don't flash just before they are injected.
.inject-me {
	display: none;
}

.injected-svg.inject-me {
	display: block;
}
