/* classes for ul */
.si-quiz{
    position: absolute;
    top: 33%;
    left: 15%;
    width: 33%;
    list-style-type: none;
    max-width: 50%;
    .si-quiz-answer{
        position: relative;
        width: 100%;
        text-align: left;
        font-size: .85rem;
        cursor: pointer;
        margin-bottom: 1rem;

        // show checkmark when hovering and when selected
        &:not([disabled]):hover .si-quiz-button img:nth-child(2){
            opacity: 1;
        }
        &.si-quiz-selected{
            .si-quiz-button img:nth-child(2) {
                opacity: 1;
            }
        }
        p {
            position: relative;
            left: 0%;
            width: 100%;
            margin-left: 1.5rem;
        }
        .si-quiz-button {
            @include v-center;
            width: 1rem;
            height: 1rem;
            left: 0;
            // box or circle
            img:nth-child(1),
            svg:nth-child(1) {
                @include center;
                height: 95%;
                width: 95%;
            }
            // checkmark
            img:nth-child(2),
            svg:nth-child(2) {
                @include center;
                height: 95%;
                width: 95%;
                top: 40%;
                left: 18%;
                opacity: 0;
            }

        }
    }
}

