// some centering and full size mixins
@mixin center {
    position: absolute;
    left: 0%;
    right: 0%;
    margin: 0 auto;
    top: 50%;
    transform: translateX(0)translateY(-50%);
}

@mixin h-center {
    position: absolute;
    left: 0%;
    right: 0%;
    margin: 0 auto;
}

@mixin v-center {
    position: absolute;
    top: 50%;
    transform: translateX(0)translateY(-50%);
}

@mixin full-size {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0%;
    left: 0%;
}
@mixin over-size {
    position: absolute;
    width: 100.5%;
    height: 100.5%;
    top: -0.5%;
    left: -0.5%;
}

// average transition length that is below the .16s refresh rate
@mixin basic-transition {
    transition: all .12s linear;
}

// hover and active states for normal elements and ones that have been vertically centered
@mixin scale-hover {
    &:not([disabled]):hover {
        transform: scale(.96);
    }
}

@mixin scale-active {
    &:not([disabled]):active {
        transform: scale(.94);
    }
}

@mixin scale-hover-vert {
    &:not([disabled]):hover {
        transform: scale(.96) translateX(0) translateY(-50%);
    }
}

@mixin scale-active-vert {
    &:not([disabled]):active {
        transform: scale(.94) translateX(0) translateY(-50%);
    }
}

// text style with null defaults to quickly set only the styles you want.  Example:
// div{
//     @include text($weight: 600, $size:.9rem)
// }
@mixin text($size: null, $lineHeight: null, $weight: null) {
    @if $size !=null {
        font-size: $size;
    }

    @if $lineHeight !=null {
        line-height: $lineHeight;
    }

    @if $weight !=null {
        font-weight: $weight;
    }
}

// defaults for most pseudo elements. Example:
// div::after {
//     @include pseudo;
//     top: -1rem;
//     left: -1rem;
//     width: 1rem;
//     height: 1rem;
// }
@mixin pseudo($display: block, $pos: absolute, $content: '') {
    content: $content;
    display: $display;
    position: $pos;
}

// Add responsive ratio to element. Example:
// div {
//     @include responsive-ratio(16, 9);
// }
@mixin responsive-ratio($x, $y, $pseudo: false) {
    $padding: unquote(($y / $x) * 100 + '%');

    @if $pseudo {
        &:before {
            @include pseudo($pos: relative);
            width: 100%;
            padding-top: $padding;
        }
    }

    @else {
        padding-top: $padding;
    }
}

// for creating perfect squares/circles using percentages.  Example:
// div{
//     width: 10%;
//     @include ratio-height(10%)
// }
@mixin ratio-height($width) {
    height: ($width * 16)/9;
}

// supposed to help with animations, needs testing
@mixin hardware($backface: true, $perspective: 1000) {
    @if $backface {
        backface-visibility: hidden;
    }

    perspective: $perspective;
}